import React from 'react';
import Person from './Person';
import { connect } from 'react-redux';
import { personPageThunk, getStatusThunk, upDateStatus }  from '../../../redux/personPage-reducer';
import { withRouter } from 'react-router';
import { compose } from 'redux';

class PersonContainer extends React.Component {
         
   componentDidMount(){
      let userId = this.props.match.params.userId;
      if( !userId ){        
         userId = this.props.authorizedUserId;
         // if(!userId){
         //    this.props.history.push("/login")
         // }
      }      
      this.props.personPageThunk(userId);
      this.props.getStatusThunk(userId);
   }

   render() {
      return (
         <Person {...this.props}
            person={this.props.person}
            status={this.props.status}
            upDateStatus={this.props.upDateStatus}/>
      );
   }
}

let mapStateToProps = (state) => ({
   person: state.personPageReduser.person,
   status: state.personPageReduser.status,
   authorizedUserId: state.auth.userId,
   isAuth: state.auth.isAuth
});

export default compose(
   withRouter,
   connect(mapStateToProps, {personPageThunk, getStatusThunk, upDateStatus })
   ) (PersonContainer);

