import React from 'react';
import s from './Friends.module.css';
import Friends_item from './FriendItem/Friends_item';

const Friends_block = (props) => {

   let friendsList = props.friends.map( f => <Friends_item name={f.name} id={f.id} key={f.id}/>)

   return(
      <div className={s.Friends_block}>
         <h4>Список друзей</h4>
         {friendsList}
      </div>
   )
}

export default Friends_block;