import React, { useEffect, useState } from "react";
import s from './About_me.module.css'

const Status = (props) => {
   let [editMode, setEditMode] = useState();
   let [status, setStatus] = useState(props.status);

   useEffect(() => {
      setStatus(props.status);
   }, [props.status])

   const activateEditMode = () => {
      setEditMode(true);
   }

   const deActivateEditMode = () => {
      setEditMode(false);
      props.upDateStatus(status)

   }
   const onStatusChange = (e) => {
      setStatus(e.target.value);
   }

   return (
      <div>
         <div className={s.statusWrapp}>
            {!editMode &&
               <div>
                  <span onDoubleClick={activateEditMode} className={s.status} > {status || "Ваш статус"} </span>
               </div>
            }
            {editMode &&
               <div>
                  <input onChange={onStatusChange} autoFocus={true} value={status} />
               </div>
            }
         </div>

         <button onClick={deActivateEditMode}>сохранить</button>
      </div>
   )
}

export default Status;