import React from 'react';
import s from './My_post.module.css';
import Post from './New_post/Post';
import { Field, reduxForm } from "redux-form";
import { required, maxLengthCreator } from '../../../../utils/validation/validation'
import { Textarea } from '../../../Common/FormsControls/FormsControls';

const maxLength10 = maxLengthCreator(10);

const FormMy_post = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <Field component={Textarea}
                name='formMyPost'
                placeholder='Enter your message'
                validate={[required, maxLength10]}
            />
            <button>Add Post</button>
        </form>
    )
}

const My_postReduxForm = reduxForm({ form: 'MyPost' })(FormMy_post);

const My_post = React.memo(props => {
    let onPostDelete = (postId) => {
        props.postDelete(postId)
    }
        
    let postsElement =
        props.postsData.map(p => <Post message={p.post} like={p.like} key={p.id} id={p.id} postDelete={onPostDelete}/>);

    let onAddPost = (values) => {
        props.addPost(values.formMyPost);
        values.formMyPost = '';
    };

    
    return (
        <div className={s.wrapp_mypost} >
            <h3>My posts</h3>
            <My_postReduxForm onSubmit={onAddPost} />
            <div className={s.posts_item}>
                {postsElement}
            </div>
        </div>
    )
});

export default My_post;