import { connect } from 'react-redux';
import { addPostActionCreator, deletePost } from '../../../../redux/personPage-reducer';
import My_post from './My_post';


let mapStateToProps = (state) => {
    return {
        newPostText: state.personPageReduser.newPostText,
        postsData: state.personPageReduser.postsData
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        addPost: (formMyPost) => {
            dispatch(addPostActionCreator(formMyPost))
        },
        postDelete: (postId) => {
            dispatch(deletePost(postId))
        }
    }
}

const My_postContainer = connect(mapStateToProps, mapDispatchToProps)(My_post);


export default My_postContainer;