import { createSelector } from "reselect"

export const getUsersSelector = (state) => {
   return state.findPageReduser.users
};

export const getUsers = createSelector( getUsersSelector, (users) => {
   return users.filter(u => true)
});

////////////////////////////////////////////////////////
export const getPageSize = (state) => {
   return state.findPageReduser.pageSize
}
export const getTotalUsersCount = (state) => {
   return state.findPageReduser.totalUsersCount
}

export const getCurrentPage = (state) => {
   return state.findPageReduser.currentPage
}

export const getIsFetching = (state) => {
   return state.findPageReduser.isFetching
}

export const getFollowingInProgres = (state) => {
   return state.findPageReduser.followingInProgres
}