import React from 'react';
import s from './Person.module.css';
import About_me from './About_me/About_me';
import My_postContainer from './My_post/My_postContainer';

const Person = (props) => {
    
    return(
        <div className={s.person_wrapp}>            
            <About_me person={props.person} status={props.status} upDateStatus={props.upDateStatus} />
            <My_postContainer
                store={props.store}
            />
        </div>
    ); 
}

export default Person;