import React from 'react';
import s from './../Dialogs.module.css';

const Message = (props) =>{
    return(
        <div>
            <div className={s.message}>
                {props.message}
                <span>
                    <button onClick={() => props.deleteMessage(props.id)}>Delete Post</button>
                </span>
            </div>
        </div>
    );
}

export default Message;