import React from 'react';
import { connect } from 'react-redux';
import Users from './Users'
import { followed, unfollowed, setCurrentPage, setFollowingInProgres, getUsersThunkCreator } from '../../../redux/findPage-reducer';
import Preloader from '../../Common/Preloader/Preloader';

import { compose } from 'redux';
import { getCurrentPage,
      getFollowingInProgres,
      getIsFetching, getPageSize,
      getTotalUsersCount, getUsers } from '../../../redux/reselectors/find-reselector';


class Find_UsersContainer extends React.Component {
   componentDidMount() {     
      this.props.getUsers(this.props.currentPage, this.props.pageSize);  
   }
   onPageChangend = (pageNumber) => {
      this.props.getUsers(pageNumber, this.props.pageSize);
   }
   render() {
      return <>
         {this.props.isFetching ?
            <Preloader />
            : null}
         <Users
            totalUsersCount={this.props.totalUsersCount}
            pageSize={this.props.pageSize}
            currentPage={this.props.currentPage}
            onPageChangend={this.onPageChangend}
            unFollowThunk={this.props.unfollowed}
            followThunk={this.props.followed}
            followingInProgres={this.props.followingInProgres}
            users={this.props.users}
         />
      </>
   }
}

const mapStateToProps = (state) => {
   return {
      users: getUsers(state),
      pageSize: getPageSize(state),
      totalUsersCount: getTotalUsersCount(state),
      currentPage: getCurrentPage(state),
      isFetching: getIsFetching(state),
      followingInProgres: getFollowingInProgres(state)
   }
}


export default compose (
   connect(mapStateToProps, 
      {
         followed,
         unfollowed,
         setCurrentPage,
         setFollowingInProgres,
         getUsers: getUsersThunkCreator
      })) (Find_UsersContainer);


