import React from 'react';
import { NavLink } from 'react-router-dom';
import Btn from './Btn_account/Btn_account';
import Btn_add_account from './Btn_add_account/Btn_add_account';

import s from './Header.module.css';

const Header = (props) => {
  return (
    <header className={s.header}>
      <h1>Шапка сайта</h1>
      <div>
        <div className={s.loginBlock}>

          {props.isAuth ?
            <div> {props.login}  
              <button onClick={ props.loginOutThunk }> Выйти </button> 
            </div> 
            : <NavLink to={'/login'}>
              <Btn />
            </NavLink>
          }

        </div>
        <NavLink to={'/createAccount'}>
          <Btn_add_account />
        </NavLink>
      </div>

    </header>
  );
}

export default Header;