import React from 'react';
import s from './Find_Users.module.css';
import imgUser from '../../../imagesApp/images/default_img_user.jpg';
import { NavLink } from 'react-router-dom';


const Users = (props) => {
   let pageCount = Math.ceil(props.totalUsersCount / props.pageSize);

   let pages = [];
   for (let i = 1; i <= pageCount; i++) {
      if (i < 20) {
         pages.push(i);
      }
   }

   return (
      <div className={s.find_users}>
         <h1 className={s.h1}>Поиск пользователей</h1>
         <div>
            {pages.map(p => {
               return <span className={props.currentPage === p && s.selectedPage}
                  onClick={(e) => { props.onPageChangend(p) }
                  }> {p} </span>
            })}
         </div>
         <div>
            {
               props.users.map(u => <div key={u.id} className={s.userCard}>
                  <div className={s.leftBlock}>
                     <NavLink to={'/person/' + u.id}>
                        <img src={u.photos.small != null ? u.photos.small : imgUser} />
                     </NavLink>
                     <div>
                        { u.followed
                           ? <button disabled = { props.followingInProgres.some(id => id === u.id)}
                                 onClick={() => {
                                    props.unFollowThunk(u.id);}}>
                              Unfollow</button>
                           : <button disabled={ props.followingInProgres.some(id => id === u.id)}
                                 onClick={() => {   
                                    props.followThunk(u.id);}}>
                              Follow</button> }
                     </div>
                  </div>
                  <div className={s.rightBlock}>
                     <div className={s.element_a}>
                        <div className={s.element_name}>{u.name}</div>
                        <div className={s.element_status}>{u.status}</div>
                     </div>
                     <div className={s.element_b}>
                        <div>{'u.location.city'}</div>
                        <div>{'u.location.country'}</div>
                     </div>
                  </div>
               </div>)
            }
         </div>

      </div>
   );
}

export default Users;