import { usersAPI } from '../api/api';

const SET_USERS = 'SET_USERS';
const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const FOLLOWING_PROGRES = 'FOLLOWING_PROGRES';


let initialState = {
   users: [],
   pageSize: 20,
   totalUsersCount: 0,
   currentPage: 1,
   isFetching: false,
   followingInProgres: []
};

const findPageReduser = (state = initialState, action) => {
   switch (action.type) {
      case FOLLOW:
         return {
            ...state,
            users: state.users.map(u => {
               if (u.id === action.userID) {
                  return { ...u, followed: true }
               }
               return u;
            })
         }
      case UNFOLLOW:
         return {
            ...state,
            // users: [...state.users]
            users: state.users.map(u => {
               if (u.id === action.userID) {
                  return { ...u, followed: false }
               }
               return u;
            })
         }
      case SET_USERS: {
         return { ...state, users: [...action.users] }
      }
      case SET_CURRENT_PAGE: {
         return { ...state, currentPage: action.currentPage }
      }
      case SET_TOTAL_USERS_COUNT: {
         return { ...state, totalUsersCount: action.count }
      }
      case TOGGLE_IS_FETCHING: {
         return { ...state, isFetching: action.isFetching }
      }
      case FOLLOWING_PROGRES: {
         return {
            ...state,
            followingInProgres: action.isFetching
               ? [...state.followingInProgres, action.userId]
               : state.followingInProgres.filter(id => id != action.userId)
         }
      }
      default:
         return state;
   }
}

export const followed = (userID) => ({ type: FOLLOW, userID });
export const unfollowed = (userID) => ({ type: UNFOLLOW, userID });
export const setUsers = (users) => ({ type: SET_USERS, users });
export const setCurrentPage = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage });
export const setTotalUsersCount = (totalUsersCount) => ({ type: SET_TOTAL_USERS_COUNT, count: totalUsersCount });
export const setISFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching })
export const setFollowingInProgres = (isFetching, userId) => ({ type: FOLLOWING_PROGRES, isFetching, userId });

export const getUsersThunkCreator = (currentPage, pageSize) => {
   return (dispatch) => {
      dispatch(setISFetching(true));
      dispatch(setCurrentPage(currentPage))
      usersAPI.getUsers(currentPage, pageSize)
         .then(data => {
            dispatch(setISFetching(false));
            dispatch(setUsers(data.items));
            dispatch(setTotalUsersCount(data.totalCount));
         });
   }
};

export const followThunk = (userId) => {
   return (dispatch) => {
      dispatch(setFollowingInProgres(true, userId));
      usersAPI.follow(userId)
         .then(data => {
            if (data.resultCode == 0) {
               followed(userId);
            }
            dispatch(setFollowingInProgres(false, userId));
         }
         );
   }
};

export const unFollowThunk = (userId) => {
   return (dispatch) => {
      dispatch(setFollowingInProgres(true, userId));
      usersAPI.follow(userId)
         .then(data => {
            if (data.resultCode == 0) {
               followed(userId);
            }
            dispatch(setFollowingInProgres(false, userId));
         }
         );
   }
};


export default findPageReduser;