import { v4 as uuidv4 } from 'uuid';

const ADD_DIALOGS = 'ADD-DIALOGS';
const DELETE_DIALOGS = 'DELETE_DIALOGS';

let initialState = {
   dialogsData: [
      { id: 1, name: "Юля" },
      { id: 2, name: "Миша" },
      { id: 3, name: "Сергей" },
      { id: 4, name: "Максим" },
      { id: 5, name: "Рома" },
      { id: 6, name: "Денис" },
      { id: 7, name: "Валера" },
      { id: 8, name: "Олег" }
   ],
   messagesData: [
      { id: 1, message: "Hi" },
      { id: 2, message: "How are your?" },
      { id: 3, message: "Yo" }
   ]
};

const dialogsPageReduser = (state = initialState, action) => {
   switch (action.type) {
      case ADD_DIALOGS: {
         return {
               ...state,
               messagesData:[
                  ...state.messagesData,
                  {id: uuidv4(), message: action.dialogsPost}]            
            }
      }
      case DELETE_DIALOGS: {
         return {
            ...state,
            messagesData: state.messagesData.filter(m => m.id !== action.messageId)
         }
      }
      default:
         return state;
   }
}
export const addDialogsActionCreator = (dialogsPost) => ({ type: ADD_DIALOGS, dialogsPost });
export const deleteMessage = (messageId) => ({ type: DELETE_DIALOGS, messageId });


export default dialogsPageReduser;