const mapStateToProps = (state)=>{
   return {
      dialogsData: state.dialogsPageReduser.dialogsData,
      messagesData: state.dialogsPageReduser.messagesData,
      newDialogsText: state.dialogsPageReduser.newDialogsText
   }
}

const mapDispatchToProps = (dispatch)=>{
   return{
         addDialogs: (dialogsPost)=>{ 
         dispatch(addDialogsActionCreator(dialogsPost));
      },
         deleteMessage: (messageId) => {
            dispatch(deleteMessage(messageId))
      }
   }
}

 const DialogsContainer =  connect(mapStateToProps, mapDispatchToProps)(Dialogs)

export default compose (
   withRouter,
   // connect(mapStateToProps,mapDispatchToProps),
   withAuthRedirect) (DialogsContainer);