import React from 'react';
import Preloader from '../../../Common/Preloader/Preloader';
import s from './About_me.module.css';
import imgUser from '../../../../imagesApp/images/default_img_user.jpg';
import Status from './Status';

const About_me = (props) => {
    if (!props.person) {
        return <Preloader />
    }
    return(
        <div className= {s.about_me}>
            <img className={s.img_top} src="https://images5.alphacoders.com/520/520133.jpg"/>        
            <div className={s.avatar}>
                <img className={s.avatar_img} src={ props.person.photos.large != null ? props.person.photos.large : imgUser}/> 
                <div className={s.persom_name}>
                    <div>{props.person.fullName}</div>
                    <div>{props.person.aboutMe}</div>
                    <div><a href="#">{props.person.contacts.vk}</a></div>
                </div>
                <Status status={props.status} upDateStatus={props.upDateStatus} />
            </div>
        </div>
    );
}

export default About_me;