import React from 'react';
import s from './Dialogs.module.css';
import DialogItem from './Dialogs_item/Dialogs_item';
import Message from './Message/Message';
import { Field, reduxForm } from "redux-form";
import { Textarea } from '../../Common/FormsControls/FormsControls';
import { required, maxLengthCreator} from  '../../../utils/validation/validation';

const maxLength20 = maxLengthCreator(20);

const DialogsForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <Field placeholder='Enter your message'
                name='dialogsPost'
                component= { Textarea } 
                validate = {[required, maxLength20]}
            />
            <button>Add Post</button>
        </form>
    )
}

const DialogsReduxForm = reduxForm({ form: 'Dialogs' })(DialogsForm);

const Dialogs = (props) => {
    let deleteMessage = (messageId) => {
        props.deleteMessage(messageId)
    }

    let dialogsElements = props.dialogsData.map(d => <DialogItem name={d.name} id={d.id} key={d.id} />);
    let messagesElements = props.messagesData.map(m => <Message message={m.message} id={m.id} key={m.id} deleteMessage={deleteMessage}/>);

    let onPost = (values) => {
        props.addDialogs(values.dialogsPost);
        values.dialogsPost='';
    }

    return (
        <div>
            <div className={s.blockMessage}>
                <DialogsReduxForm onSubmit={onPost} />
            </div>
            <div className={s.dialogs}>
                <div className={s.dialogs_items}>
                    {dialogsElements}
                </div>
                <div className={s.messages}>
                    {messagesElements}
                </div>
            </div>
        </div>
    );
}



export default Dialogs;