import React from "react";
import { Field, reduxForm } from "redux-form";
import { Input } from "../Common/FormsControls/FormsControls"; 
import { required, maxLengthCreator} from "../../utils/validation/validation";
import { Redirect } from "react-router";

import s from "../Common/FormsControls/FormControls.module.css"


const maxLength = maxLengthCreator(50);

const LoginForm = (props) => {
   return (
      <form onSubmit={props.handleSubmit}>
         <div>
            <Field placeholder={'Email'} 
               name={'email'} component = { Input } validate = {[required, maxLength]} />
         </div>
         <div>
            <Field placeholder={'Пароль'}
               name={'password'} type = {'password'} component = { Input } validate = {[required, maxLength]}/>
         </div>
         <div>
            <Field type={'checkbox'} name={'rememberMe'} component = { Input } /> Запомнить меня
         </div>
         { props.error && <div className={s.formSumaryError}> {props.error} </div> }
         <div>
            <button>Войти</button>
         </div>
      </form>
   )
};

const LoginReduxForm = reduxForm({form: 'Login'})(LoginForm);

const Login = (props) => {
   const onSubmit = (formData) => {
      props.loginThunk(formData.email, formData.password, formData.rememberMe)
   
   }

   if(props.isAuth){
      return <Redirect to = {'/person'} />
   }

   return (
      <div>
         <h3>Заполните форму входа</h3>
         <LoginReduxForm onSubmit={onSubmit} />
      </div>
   )
};


export default  Login;