import { v4 as uuidv4 } from 'uuid';
import { personAPI } from '../api/api';

const ADD_POST = 'ADD-POST';
const SET_USERS_PERSON = 'SET_USERS_PERSON';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';

let initialState = {
   postsData: [
      { id: 1, post: "Hi, how are you?", like: "15" },
      { id: 2, post: "It`s my first post.", like: "20" }
   ],
   person: null,
   status: ""
};

const personPageReduser = (state = initialState, action) => {
   switch (action.type) {
      case ADD_POST: {
         return {
            ...state,
            postsData: [
               ...state.postsData,
               { id: uuidv4(), post: action.formMyPost, like: 33 }]
         }
      }
      case DELETE_POST:
         return  {
            ...state,
            postsData: state.postsData.filter(p => p.id !== action.postId)
         }
      case SET_USERS_PERSON: {
         return {
            ...state,
            person: action.person.data
         }
      }
      case SET_STATUS: {
         return {
            ...state,
            status: action.status
         }
      }
      default: {
         return state;
      }
   }
}

export const setUsersPerson = (person) => ({ type: SET_USERS_PERSON, person });
export const addPostActionCreator = (formMyPost) => ({ type: ADD_POST, formMyPost});
export const deletePost = (postId) => ({ type: DELETE_POST, postId });
export const setStatus = (status) => ({ type: SET_STATUS, status });

export const personPageThunk = (userId) => {
   return (dispatch) => {
      personAPI.getPersonPage(userId)
         .then(data => {
            dispatch(setUsersPerson(data));
         });
   }
}

export const getStatusThunk = (userId) => (dispatch) => {
   personAPI.getStatus(userId)
      .then(response => {
         if (response.data === null) {
            return;
         }
         dispatch(setStatus(response.data));
      });
}

export const upDateStatus = (status) => (dispatch) => {
   personAPI.upDateStatus(status)
      .then(response => {
         if (response.data.resultCode === 0) {
            dispatch(setStatus(status));
         }
      });
}

export default personPageReduser;