import React from 'react';
import s from './Side_bars.module.css';
import {NavLink} from 'react-router-dom';
import Friends_blockContainer from './Friends_block/Freinds_blockContainer';
import NavContainer from './Nav/NavContainer';

const Side_bars = (props) => {
    return(
      <div className={s.side_bars}>
        <NavContainer />
        <button>
          <NavLink to="/findusers" activeClassName={s.active}> Поиск Пользоватилей</NavLink>
        </button>
        <Friends_blockContainer 
          // store={props.store}
        />
      </div>
    );
  } 

  export default Side_bars;