import React from 'react';
import s from './Main_page.module.css';
import Side_bars from './Side_bar/Side_bars';
import DialogsContainer from './Dialogs_page/DialogsContainer';
import { Route, } from 'react-router-dom';
import News from './News_page/News';
import Music from './Music_page/Music';
import Sport from './Sport_page/Sport';
import Find_UsersContainer from './Find_Users/Find_UsersContainer'
import PersonContainer from './Person/PersonContainer';
import LoginContainer from '../Login/LoginContainer';



const Main_page =() =>{

    return (
        <main className={s.grid_container}>
                  
          <Side_bars />
          <Route path='/dialogs' render={ () => <DialogsContainer/>} />
          <Route path='/person/:userId?' render={ () => <PersonContainer />} />            
          <Route path='/news' render={ () => <News />} />
          <Route path='/music' render={ () => <Music />} />
          <Route path='/sport' render={ () => <Sport />} />
          <Route path='/findusers' render={ () => <Find_UsersContainer />} /> 
          <Route path='/login' render={() => <LoginContainer /> } />        
        </main>
      ) 
    } 

export default Main_page;
