import React from 'react';
import { NavLink } from 'react-router-dom';
import s from './Friends_item.module.css';

const Friends_item = (props) => {
   let path = "/dialogs/" + props.id;

   return (
      <div className={s.Friends_item}>
         <NavLink to={path} activeClassName={s.active}> <img className={s.img}/> {props.name} </NavLink>
      </div>
   )
   }

   export default Friends_item;