import React from 'react';
import s from './Nav.module.css';
import {NavLink} from 'react-router-dom';

const Nav = (props) => {
   return (
      <div className={s.nav}>
         <nav>
            <div className={s.item}>
               <NavLink to="/person" activeClassName={s.active} >Профиль</NavLink>
            </div>
            <div className={s.item}>
               <NavLink to="/dialogs" activeClassName={s.active} >Сообщение</NavLink>
            </div>
            <div className={s.item}>
               <NavLink to="/news" activeClassName={s.active} >Новости</NavLink>
            </div>
            <div className={s.item}>
               <NavLink to="/music" activeClassName={s.active} >Музыка</NavLink>
            </div>
            <div className={s.item}>
               <NavLink to="/sport" activeClassName={s.active} >Спорт</NavLink>
            </div>
         </nav>
      </div>
   );
}

export default Nav;